@import "../../global.scss";

.intro{
  background-color: white;
  display: flex;

  @include mobile{
    flex-direction: column;
    align-items: center;
  }

  .left{
    flex: 0.5;
    overflow: hidden;

    .imgContainer{
      width: 700px;
      height: 700px;
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      float: right;
      overflow: hidden;

      @include mobile{
        align-items: flex-start;
      }


      img{
        height: 100%;

        @include mobile{
          height:45%;
          border-radius: 50%;
        }
      }
    }
  }
  .right{
    flex: 0.5;
    position: relative;
    justify-content: center;

    .wrapper{
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile{
        padding-left: 0;
        align-items: center;
        height: calc(100% - 70px);
      }

      h1{
        font-size: 60px;
        margin: 10px 0;

        @include mobile{
          font-size: 40px;
        }
      }
      h2{
        font-size: 35px;

        @include mobile{
          font-size: 27px;
        }
      }
      h3 {
        font-size: 30px;

        @include mobile{
          font-size: 20px;
        }

        span {
          font-size: inherit;
          color: crimson;
        }

        .ityped-cursor {
          animation: blink 1s infinite;
        }

        @keyframes blink {
          50%{
            opacity: 1;
          }
          100%{   //same as 'to' (means to end of animation)
            opacity: 0;
          }
        }
      }
    }
    a {
      position: absolute;
      bottom: 10px;
      left: 45%;

      img {
        width: 30px;
        animation: arrowBlink 2s infinite;
      }

      @keyframes arrowBlink{
        to{     //same as '100%' (means to end of animation)
          opacity: 0;
        }
      }
    }
  }
}