@import "../../global.scss";

.projects{
  background-color: rgb(119, 189, 235);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  @include mobile{
    height: 100vh;
    overflow: hidden;
  }

  .arrow{
    height: 60px;
    position: absolute;
    cursor: pointer;

    @include mobile{
      display: none;
    }

    &.left{
      left: 100px;
      transform: rotateY(180Deg);
    }
    &.right{
      right: 100px;
    }
  }
  .slider{
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;

    @include mobile{
      height: 100vh;
      flex-direction: column;
      justify-content: center;
    }


    .container{
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;


      .item{
        width: 700px;
        height: 100%;
        background-color: white;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile{
          width: 80%;
          height: 150px;
          margin: 10px 0 0 0;
        }
        
        .left{
          flex: 4;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          @include mobile{
            height: 100%;
            margin-left: 10px;
          }

          .leftContainer{
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;


            .imgContainer{
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background-color: orange;
              display: flex;
              align-items: center;
              justify-content: center;

              img{
                width: 25px;
              }
            }
            h2{
              font-size: 20px;

              @include mobile{
                font-size: 15px;
              }
            }
            p{
              font-size: 13px;
              @include mobile{
                display: none;
              }
            }
            span{
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;
              cursor: pointer;

              @include mobile{
                font-size: 9px;
              }
            }
          }
        }

        .right{
          flex: 8;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;

          @include mobile{
            font-size: 17px;
            flex: 6;
          }

          img{
            width: 400px;
            transform: rotate(-10deg);
          }
        }
      }
    }
  }

}